@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body{font-family:"Roboto",sans-serif;font-weight:300;background:#f5f5f5;color:#333;height:100%}h1,h2,h3,h4,h5,h6,.h1,.h2,.h3,.h4,.h5,.h6{font-family:"Montserrat",sans-serif !important;font-weight:700;margin-bottom:2rem}@media (max-width: 991.98px){h1,.h1{font-size:2rem}}.btn{font-family:"Roboto",sans-serif;font-weight:300;text-transform:uppercase;font-size:1rem;letter-spacing:1px;min-width:120px;border-radius:50px}.btn:focus{outline:0;box-shadow:none !important}.btn.btn-primary{background:#CC262D;border-color:#CC262D}.btn.btn-primary:active,.btn.btn-primary:not(:disabled):not(.disabled):active{background:#a11e24;border-color:#a11e24}.btn.btn-icon{min-width:auto;border:none;background-color:inherit;border-radius:0}a{color:#333;position:relative;text-decoration:none}a.primary-link:after{content:'';height:2px;position:absolute;bottom:-5px;left:0;width:0%;background:#CC262D;transition:0.2s}a.primary-link:hover{color:#CC262D;text-decoration:none}a.primary-link:hover:after{width:100%}.page-content{width:100%;height:auto !important;min-height:100vh;padding:50px 15px 50px 115px;margin:0 auto}fieldset{margin-bottom:15px}.form-control{border-radius:0;border-top:0;border-left:0;border-right:0;border-bottom:1px solid #ced4da;padding:.375rem 0;font-weight:300}.form-control:focus{border-bottom-color:#CC262D;outline:0;box-shadow:none}.form-control::-webkit-input-placeholder{color:#cccccc}.form-control:-ms-input-placeholder{color:#cccccc}.form-control::-ms-input-placeholder{color:#cccccc}.form-control::placeholder{color:#cccccc}.form-label{text-transform:uppercase;font-size:12px;font-weight:400;font-family:"Montserrat",sans-serif}.input-required{color:#CC262D;font-weight:bold}.input-search .form-control{border-radius:50px 0 0 50px;border:1px solid #CC262D;padding:.375rem .75rem}.input-search .btn{min-width:auto !important;padding:.375rem 2rem}

